@import "styles/main.module";

.App {
  width: 100%;
  height: 100%;
  min-width: $site-min-width-desk;
  min-height: $site-min-height-desk;
  overflow-x: auto;
  overflow-y: hidden;
}

@include with-mobile {
  .App {
    min-width: auto;
    min-height: auto;
    overflow-x: hidden;
    overflow-y: auto;
  }
}
